@import '../../style/';

.modal {
    visibility: hidden;
    opacity: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: $bg-modal;
    transition: 0.4s all;
    z-index: 10;

    &__title {
        padding: 25px 35px;
        font-size: 25px;
        font-weight: nth($font-weight, 1);
        background-color: $white;
        border: 2px solid #8cc63f;
        border-radius: 24px;
        color: #2f4f3c;
    }

    &.active {
        visibility: visible;
        opacity: 1;
    }
}